import {Component, Vue} from "vue-property-decorator";
const { GoogleLogin } = require('vue-google-login');

@Component({
    components: { GoogleLogin }
})
export default class Login extends Vue {

    params: any = {
        client_id: "545324358636-40oml219h11b1st6aitu67ub5pd6sm9l.apps.googleusercontent.com",
        scope: "https://www.googleapis.com/auth/books"
    }
    renderParams: any = {
        width: 250,
        height: 50,
        longtitle: true
    }
    onSuccess(googleUser: any) {
        console.log("Login", googleUser);

        const User = {
            name: googleUser.getBasicProfile().getName(),
            image: googleUser.getBasicProfile().getImageUrl()
        }

        localStorage.setItem('User', JSON.stringify(User));
        localStorage.setItem('access_token', googleUser.getAuthResponse(this.params).access_token);
        this.$router.push('/Home');
    }
    onFailure(error: any) {
        alert("Login onFailure")
    }
}